.post-container {

    display: flex;
    margin-top: 2.5em;
    margin-bottom: 2.5em;
    border-radius: 1em;
    min-width: 40em;
    transition: height 1s ease;
    min-height: 25em;
}


.post-header h2, .post-header p {
    margin-top: 0;
    margin-bottom: 0;
}
.post-header h2 {
    padding-top: 0.5em;
}
.post-header p {
    padding-bottom: 0.5em;
}


.post-data {
    display: flex;
    justify-content: space-around;
    border-bottom-left-radius: 1em;
    height: 3.5em;
}

.post {
    border-radius: 1em;
    width: 30vw;
    min-width:40em;

}

.post-body img, .post-body video {
    width: 100%;
}




.post-data-num-comments {
    cursor:pointer;
    text-decoration: underline;
    transition: text-decoration-color 0.2s ease, color 0.2s ease, transform 0.2s ease;;
}



  .post-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left:1em;
    padding-right: 1em;
    border-top-left-radius: 1em;
  }

  .post-body {
display: flex;
min-height: 25em;
  }

  .post-body-self-text {
    padding-left: 0.5em;
    padding-right: 0.5em;

  }

  .post-comments {
    overflow: scroll;
    z-index: 1;
    border-top-right-radius: 1em;
    border-bottom-right-radius: 1em;

}


.post-body p {
  margin-left: auto;
  margin-right: 4em;
}

.subreddit-link {
  display: inline;
  cursor:pointer;
  text-decoration: underline;
  transition: text-decoration-color 0.2s ease, color 0.2s ease;
}



.activeComments .post .post-data-num-comments{
  position: relative;
  transform:translateY(-0.7em);

}

.post-data select {
  border: none;
  text-align: right;
  position: absolute;
}

.post-data-score-button {
  display: inline;
}
.post-data-score {
  display: flex;
  align-items: center;
}
.upvote {
  padding-left: 0.5em;
}
.downvote {
  padding-right: 0.5em;
}