
.comment {
    display: flex;
    flex-direction: column;
}

.comment-data {
    display: flex;
    justify-content: space-between;


}

.comment-body {
    flex-basis: 100%;
    margin: 0;
    padding-left: 1em;
    padding-right: 1em;
}

.comment-author {
    margin: 0;
    margin-top: 0.5em;

}
.comment-data-score, .comment-data-created {
    margin: 0;
    margin-bottom: 0.5em;

}

.reply {
    margin-left: 2em;
}
.comment-data-score-button {
    display: inline;
  }
  .comment-data-score {
    display: flex;
    align-items: center;
  }

  .comment-author {
    padding-left: 1em;
}
.comment-data-created {
    padding-right: 1em;
}
.comment-data-score {
    padding-left: 1em;
}
.proper-score {
    margin-top: 0;
    margin-bottom: 0;
}