.sidebar {
    display: flex;
    flex-direction: column;
    width: 40vw;
    height: 70vh;
    padding-top: 5vw;
    padding-left: 5vh;
    padding-right: 5vw;
    position: fixed;
    right: 10px;
    z-index:5;
}

.sidebar, .sidebar * {
    border-top-left-radius: 1em;
    border-bottom-left-radius: 1em;
}