nav {
    margin-left: 0.5em;
    margin-right: 0.5em;
}

nav {
    display: flex;
    justify-content: center;
    margin-bottom: auto;
    margin-top: auto;
  }
  
nav li {
    cursor: pointer;
    list-style: none;
    width: 0.5em;
    padding: 0 2.58rem .33rem 2.58rem;
    line-height: 1.13;
    text-align: center;
    font-weight: 600;
    font-size: .83rem;
    transition: color .25s ease;
  }
