:root {
  --background-color: #F5F5F5;
  --text-color: #F5F5F5;
  --header-color: #3D5A80;
  --accent-color: #EFA28E;
  --selected-color: #EE6C4D;
  

}

* {
  outline: 0px solid red;
  background-color: var(--background-color);
  color: var(--text-color);
}

.post-header, .post-header * , .sidebar, .sidebar *, header, header *, footer, footer *, .post-data, .post-data *, .post-body, .post-body *, .post-comment-section, .post-comment-section *, .post-comments, .post-comments *  {
  background-color: var(--header-color);
}


.post-data-num-comments:hover {
  text-decoration-color: var(--accent-color);
    color: var(--accent-color);
  }
  

.subreddit-search-bar {
  outline: 2px solid var(--text-color);
}

.comment-thread {
  outline: 3px solid var(background-color);
}

.comment-data p, .comment-author {
  color: var(--accent-color); 
}

.reply {
  border-left: 1px solid var(--background-color);
}

.comment-author-op {
  color: rgb(255, 187, 0);
  text-decoration:underline;
}

.subreddit-link:hover {
  text-decoration-color: var(--accent-color);
  color: var(--accent-color);
}
.activeComments{
  color: var(--accent-color);
  text-decoration-color: var(--accent-color);

}


nav li {
  border-bottom: 1px solid var(--text-color);
}

nav li:hover {
  color: var(--accent-color);
  border-bottom: 1px solid var(--accent-color)
}

nav li.active,
nav li.active:hover {
  border-bottom: 1px solid var(--selected-color);
  color: var(--selected-color);
}
.subreddit-search-results-result:hover {
  color: var(--accent-color);
}
.activeComments .post .post-data-num-comments {
  color: var(--selected-color)
}
.activeComments .post .post-data-num-comments:hover {
  color: var(--accent-color)
}